





const Homologos=[
[5,14],
[14,5],
[0,11],
[8,5],
[11,0],
[10,4],
[4,10],
[15,6],
[6,15],
[2,12],
[12,2]
]


const buscarH=(num)=>{
let r;
for(let i=0;i<Homologos.length;i++){

if(num==Homologos[i][0]){

r= Homologos[i][1]

}
}
return r;

 


}
const CColor=(num,color)=>{
  
const img = document.getElementById('Arrow'+num);
const arrow="data:image/png;base64,";
const arrow_green='data:image/png;base64,'
const arrow_yellow='data:image/png;base64,'
const arrow_Left="data:image/png;base64,"
const arrow_Left_green='data:image/png;base64,'
const arrow_Left_yellow='data:image/png;base64,'
const arrow_right="data:image/png;base64,"
const arrow_right_green='data:image/png;base64,'
const arrow_right_yellow='data:image/png;base64,'
console.log(img.getAttribute("src"));
switch (img.getAttribute("src")) {

    case arrow:
        if(color==1){img.src=arrow_green}
        if(color==2){img.src=arrow_yellow;}
        break;
case arrow_green:
            if(color==1){img.src=arrow_green}
            if(color==2){img.src=arrow_yellow;}
            break;
case arrow_yellow:
                if(color==1){img.src=arrow_green}
                if(color==2){img.src=arrow_yellow;}
                break;
case  arrow_Left: 
if(color==1){img.src=arrow_Left_green;}
if(color==2){img.src=arrow_Left_yellow;}
break;
case  arrow_Left_green: 
if(color==1){img.src=arrow_Left_green;}
if(color==2){img.src=arrow_Left_yellow;}
break;
case  arrow_Left_yellow: 
if(color==1){img.src=arrow_Left_green;}
if(color==2){img.src=arrow_Left_yellow;}
break;
case  arrow_right:

    if(color==1){img.src=arrow_right_green;}
    if(color==2){img.src=arrow_right_yellow;}

break;
case  arrow_right_green:

    if(color==1){img.src=arrow_right_green;}
    if(color==2){img.src=arrow_right_yellow;}

break;
case  arrow_right_yellow:

    if(color==1){img.src=arrow_right_green;}
    if(color==2){img.src=arrow_right_yellow;}

break;
default:
        break;
}        




}

export {buscarH as default,
     CColor}